@for $i from 1 through $columns {
    .col-#{$i} {
        flex: 0 0 (100% / 12) * $i; //grow shrink base
        width: (100% / 12) * $i;
    }

    @include responsive(765px) {
        .col-#{$i} {
            flex: 0 0 100%; //grow shrink base
            width: 100%;
        }
     
    }
}


.row {
    display: flex;
    flex-wrap: wrap;
}